 */

.wp-block-create-block-card-carousel {
	background-color: #eeeeee;
}

.image-carousel-block {
	max-width: 1920px;
	margin: 0 auto;
	padding: 110px 80px;

	.blockCTA {
		width: 100%;
		display: flex;
		justify-content: end;
		margin-top: 120px;
	}
}

.image-carousel-slider {
	position: relative;

	&::before,
	&::after {
		content: "";
		position: absolute;
		top: 0;
		width: 100px;
		height: 100%;
		z-index: 1;
		pointer-events: none;
	}

	&::before {
		left: -10px;
		background: linear-gradient(
			to right,
			rgba(238, 238, 238, 1),
			rgba(238, 238, 238, 1),
			rgba(238, 238, 238, 0.5),
			rgba(238, 238, 238, 0)
		);
	}

	&::after {
		right: -10px;
		background: linear-gradient(
			to left,
			rgba(238, 238, 238, 1),
			rgba(238, 238, 238, 0)
		);
	}
	.slick-list {
		padding: 0 80px 0 80px;
	}
}

.image-carousel-card {
	.image-carousel-card-inner {
		background: white;
		padding: 20px 24px;
		height: 500px;
		margin: 0 2px;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	.card-title {
		font-size: 14px;
		font-weight: 300;
		line-height: 21px;
	}
}

.carousel-header {
	display: flex;
	flex-direction: row;
	justify-content: space-between;

	p {
		font-family: var(--wp--preset--font-family--joly-display);
		font-size: 35px;
		font-weight: 400;
		font-style: italic;
		line-height: 52px;
		color: #262626;
		text-transform: capitalize;
	}

	/* Flex container for arrows and progress bar */
	.carousel-controls {
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: space-between;
		margin-bottom: 90px;

		/* Styling for the arrows */
		.carousel-arrow {
			background: none;
			border: none;
			cursor: pointer;
			padding: 5px;

			&.carousel-arrow-left {
				svg {
					transform: rotate(180deg);
				}
			}

			svg {
				fill: #007cba;
				width: 47px;
				height: 24px;
			}
		}

		/* Styling for the progress bar */
		.carousel-progress-bar {
			height: 2px;
			background-color: #c9c8c8;
			position: relative;
			margin: 0 10px;
			width: 160px;

			.progress-bar-inner {
				height: 100%;
				width: 0%;
				background-color: #646464;
				transition: width 0.3s ease;
			}
		}
	}
}
